.register{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.form{
  width: 100%;
  max-width: 533px;
  height: 624px;
  backdrop-filter: blur(100px);
  background: #ffffff1a;
  border-radius: 8px;
  box-shadow: 0 4px 60px #00000040;
  padding: 60px;
  gap: 40px;
}
.containerLogo{
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 40px;
}
.wrapperInput{
  display: flex;
  flex-direction: column;
  gap: 40px;
}
.input{
  width: 100%;
  outline: none;
  border: none;
  border-bottom: 1px solid #ffffff66;
  background: transparent;
  padding-bottom: 8px;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 18px;
  color: rgba(255, 255, 255, 0.6);
  height: 35px;
  
}
.user{
    background: url('../../images/user_icon.png') no-repeat left center;  
    padding-left: 44px;
    background-position-y: 20%;
    background-position-x: 10px;
  
}
.mail{
  background: url("../../images//email_icon.png") no-repeat left;
  padding-left: 44px;
  background-position-y: 20%;
  background-position-x: 10px;
}
.lock{
  background: url("../../images//lock_icon.png") no-repeat left;
  padding-left: 44px;
  background-position-y: 20%;
  background-position-x: 10px;
}
.button{
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 40px;
  margin-bottom: 40px;
}
.button > button{
  border-radius: 20px;
  width: 300px;
  height: 50px;
  box-shadow: 1px 9px 15px 0 rgba(0, 0, 0, 0.2);
  background: linear-gradient(167deg, #ffc727 0%, #9e40ba 61.46%, #7000ff 90.54%);
  outline: none;
  border: none;
  cursor: pointer;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 18px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
}
.link{
  width: 100%;
  text-align: end;
}
.link > a{
  text-decoration: none;
  color: #ffff;
  transition: color 0.2s ease-in;
}
.link > a:hover{
  color: #cd853f
}
.error {
    color: #ff0000;
    font-size: 12px;
    margin-top: 5px;
    font-family: var(--font-family);
  }